import { useState, useRef, useEffect } from "react";
import { connect } from "react-redux";
import { nanoid } from "nanoid";

import { adapter } from "../../../../../../App";
import PhotoTemplate from "./PhotoTemplate/PhotoTemplate";

import './Form.css';

const Form = ({ selectedMarker, selectedMarkerAgain, isVisibleMarker }) => {
    const [photosArray, setPhotosArray] = useState([]); //      .   ,     
    const [clearFiles, setClearFiles] = useState(false);
    const photosForm = useRef();
    const form = useRef()

        if (!selectedMarkerAgain && selectedMarker && isVisibleMarker && clearFiles) {
            form.current.reset();
            if (photosArray.length) setPhotosArray([])
        };


    const submitData = event => {
        event.preventDefault();

        if (photosArray.length) {
            const validationResult = validation(photosArray);
            if (!validationResult) {    //   ,    true,  undefined
                return;
            }
        };

        const animationList = [form.current['description'], form.current['short_description']];

        if (!animationList[0].value && !animationList[1].value) {   //      ,   
            animationList.forEach(e => e.classList.add('animation'));
            setTimeout(() => animationList.forEach(e => e.classList.remove('animation')), 3000);    //   ,  ,   3 ,  

            alert('  -  ');
            return;
        }

        const formData = new FormData(form.current);

        adapter
            .post('http://127.0.0.1:7000/api/petitions/', formData)
    }

    const uploadPhotos = event => {
        console.log(event)
        const photos = event.target

        if (photos.files.length > 10) {     //   ,     10
            photos.files = (new DataTransfer() || new ClipboardEvent('').clipboardData).files;      //  .  FileList'a  0
            alert('     10.');
            return;
        };

        let photosList = [];

        Object.values(photos.files).forEach(file => {
            for (const photo of photosArray) {    //          ,    
                if (photo.name === file.name) {     //   ,        
                    return
                }
            };

            photosList.push({
                name: file.name,
                type: file.type,
                file: file,
                size: file.size
            })
        });

        setPhotosArray([...photosArray, ...photosList])
    }

    const validation = photos => {   // 
        for (const photo of photos) {
            if (photo.size > 5 * 1024 * 1024) {     //   .     5 
                alert('      5 .')
                return;
            };
            if (!['image/jpeg', 'image/jpg', 'image/png', 'image/gif'].includes(photo.type)) {  //    
                alert('     .  : jpeg, jpg, png, gif.')
                return;
            };
        };
        return true;
    }

    const closePhoto = event => { //       ,     

        //     - ,    ()   FileList' ,        FileList,          

        const nameOfRemovedFile = event.target.previousSibling.innerText;    // ,     

        const fileList = new DataTransfer() || new ClipboardEvent('').clipboardData;  //  FileList,      

        setPhotosArray(photosArray.filter(photo => {
            if (photo.name !== nameOfRemovedFile) {
                fileList.items.add(new File([photo.file], photo.name, {type: photo.type}))

                return true
            }
        }));
        photosForm.files = fileList.files;  //  ,   FileList     
    }

    console.log(photosArray)

    return (
        <form
            action="http://127.0.0.1:7000/api/petitions/"
            method="POST"
            id="form"
            autoComplete="off"
            ref={form}
        >
            <h1> </h1>
            <div className="table">
                <div className="table_row">
                    <span></span>
                    <input
                        type="hidden"
                        name="coordinates"
                        maxLength="32"
                        required
                        id="id_coordinates"
                        defaultValue={selectedMarker?.coodinates}
                    />
                </div>
                <div className="table_row">
                    <span> </span>
                    <input
                        type="text"
                        name="short_description"
                        maxLength="32"
                        required
                        id="id_short_description"
                    />
                </div>
                <div className="table_row">
                    <span></span>
                    <textarea
                        name="description"
                        cols="40"
                        rows="10"
                        required
                        id="id_description"
                    ></textarea>
                </div>
                <div className="table_row">
                    <label htmlFor="id_photo"> </label>
                    <input
                        type="file"
                        name="photos"
                        accept="image/*"
                        id="id_photo"
                        multiple
                        ref={photosForm}
                        onChange={e => uploadPhotos(e)}
                    />
                    {photosArray[0] && photosArray.map(photo =>
                        <PhotoTemplate
                            name={photo.name}
                            type={photo.type}
                            func={closePhoto}
                            key={nanoid()}
                        />
                    )}
                </div>
            </div>
            <button type="submit" onClick={submitData}></button>
        </form>
    )
}

const mapState = (state) => {
    return {
        selectedMarker: state.selectedMarker,
        selectedMarkerAgain: state.selectedMarkerAgain,
        isVisibleMarker: state.isVisibleMarker
    }
}

export default connect(mapState, null)(Form)